/**
 * Ionic
 * @see: http://ionicframework.com/docs/theming/
 */

:root {
  /** typography */
  /*
  --ion-font-family: var(--font-family-default);
  --ionicon-stroke-width: 16px;
  */

  /** text color */
  --ion-text-color: var(--ion-color-dark);
  --ion-text-color-rgb: var(--ion-color-dark-rgb);

  /** colors */
  --ion-color-primary: #f7b49b;
  --ion-color-primary-rgb: 247,180,155;
  --ion-color-primary-contrast: #000000;
  --ion-color-primary-contrast-rgb: 0,0,0;
  --ion-color-primary-shade: #d99e88;
  --ion-color-primary-tint: #f8bca5;

  --ion-color-secondary: #c1e0ce;
  --ion-color-secondary-rgb: 193,224,206;
  --ion-color-secondary-contrast: #000000;
  --ion-color-secondary-contrast-rgb: 0,0,0;
  --ion-color-secondary-shade: #aac5b5;
  --ion-color-secondary-tint: #c7e3d3;

  --ion-color-tertiary: #aaa1cf;
  --ion-color-tertiary-rgb: 170,161,207;
  --ion-color-tertiary-contrast: #000000;
  --ion-color-tertiary-contrast-rgb: 0,0,0;
  --ion-color-tertiary-shade: #968eb6;
  --ion-color-tertiary-tint: #b3aad4;

  --ion-color-success: #2dd36f;
  --ion-color-success-rgb: 45,211,111;
  --ion-color-success-contrast: #ffffff;
  --ion-color-success-contrast-rgb: 255,255,255;
  --ion-color-success-shade: #28ba62;
  --ion-color-success-tint: #42d77d;

  --ion-color-warning: #ffc409;
  --ion-color-warning-rgb: 255,196,9;
  --ion-color-warning-contrast: #000000;
  --ion-color-warning-contrast-rgb: 0,0,0;
  --ion-color-warning-shade: #e0ac08;
  --ion-color-warning-tint: #ffca22;

  --ion-color-danger: #eb445a;
  --ion-color-danger-rgb: 235,68,90;
  --ion-color-danger-contrast: #ffffff;
  --ion-color-danger-contrast-rgb: 255,255,255;
  --ion-color-danger-shade: #cf3c4f;
  --ion-color-danger-tint: #ed576b;

  --ion-color-dark: #000000;
  --ion-color-dark-rgb: 0,0,0;
  --ion-color-dark-contrast: #ffffff;
  --ion-color-dark-contrast-rgb: 255,255,255;
  --ion-color-dark-shade: #000000;
  --ion-color-dark-tint: #1a1a1a;

  --ion-color-medium: #9a9a9a;
  --ion-color-medium-rgb: 154,154,154;
  --ion-color-medium-contrast: #000000;
  --ion-color-medium-contrast-rgb: 0,0,0;
  --ion-color-medium-shade: #888888;
  --ion-color-medium-tint: #a4a4a4;

  --ion-color-light: #e6e6e6;
  --ion-color-light-rgb: 230,230,230;
  --ion-color-light-contrast: #000000;
  --ion-color-light-contrast-rgb: 0,0,0;
  --ion-color-light-shade: #cacaca;
  --ion-color-light-tint: #e9e9e9;

  /**
   * additional color:
   * https://ionicframework.com/docs/theming/colors#adding-colors
   */
  --ion-color-main: #eb5b40;
  --ion-color-main-rgb: 235,91,64;
  --ion-color-main-contrast: #ffffff;
  --ion-color-main-contrast-rgb: 255,255,255;
  --ion-color-main-shade: #cf5038;
  --ion-color-main-tint: #ed6b53;
}

/** registering additional color */
.ion-color-main {
  --ion-color-base: var(--ion-color-main);
  --ion-color-base-rgb: var(--ion-color-main-rgb);
  --ion-color-contrast: var(--ion-color-main-contrast);
  --ion-color-contrast-rgb: var(--ion-color-main-contrast-rgb);
  --ion-color-shade: var(--ion-color-main-shade);
  --ion-color-tint: var(--ion-color-main-tint);
}

